.fact-form {
  background-color: #44403c;
  margin-bottom: 40px;
  padding: 16px 32px;
  display: flex;
  align-items: center;
  gap: 16px;
  border-radius: 16px;
  input,
  select {
    width: 220px;
    background-color: #78716c;
    border: none;
    border-radius: 100px;
    padding: 16px;
    font-size: 18px;
    color: inherit;
    font-family: inherit;
  }
  input::placeholder {
    color: #a8a29e;
  }

  input:first-child {
    flex-grow: 1;
  }

  span {
    font-weight: 600;
    font-size: 18px;
    margin-right: 18px;
  }
}
