* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #292524;
  color: #fafaf9;
  font-family: "Sono", sans-serif;
  padding: 3rem 4rem 0;
}

#root {
  max-width: 1500px;
  margin: 0 auto;
}

.header {
  margin-bottom: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main {
  height: calc(100vh - 168px);
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 3rem;
}

section {
  overflow: scroll;
  padding-bottom: 40px;
}

.btn {
  border: none;
  font-family: "Coiny", sans-serif;
  line-height: 1;
  text-transform: uppercase;
  font-size: 1rem;
  padding: 1rem 0 0.9rem;
  background-color: #f982aa;
  color: inherit;
  border-radius: 6.2rem;
  cursor: pointer;
  transition: 0.3s;
}

.btn:hover {
  transform: scale(90%);
}

.btn-large {
  font-size: 1.3rem;
  padding: 1.3rem 2rem 1rem;
}

.btn-share {
  margin-bottom: 2rem;
  padding: 1rem 5rem;
}

.btn-all-categories {
  margin-bottom: 1rem;
  width: 100%;
}

.btn-category {
  width: 100%;
  background-image: none;
}

.vote-buttons {
  margin-left: auto;
  flex-shrink: 0;
  display: flex;
  gap: 0.5rem;
}
.vote-buttons button {
  border: none;
  background-color: #78716c;
  font-size: 1.1rem;
  padding: 0.3rem 0.7rem;
  border-radius: 6.2rem;
  color: inherit;
  font-family: inherit;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
}
.vote-buttons button:hover {
  background-color: #292524;
}

.fact-form {
  background-color: #44403c;
  margin-bottom: 40px;
  padding: 16px 32px;
  display: flex;
  align-items: center;
  gap: 16px;
  border-radius: 16px;
}
.fact-form input,
.fact-form select {
  width: 220px;
  background-color: #78716c;
  border: none;
  border-radius: 100px;
  padding: 16px;
  font-size: 18px;
  color: inherit;
  font-family: inherit;
}
.fact-form input::placeholder {
  color: #a8a29e;
}
.fact-form input:first-child {
  flex-grow: 1;
}
.fact-form span {
  font-weight: 600;
  font-size: 18px;
  margin-right: 18px;
}

.hidden {
  display: none !important;
}

.logo {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.logo img {
  width: 5rem;
  height: 5rem;
}

h1 {
  font-size: 3rem;
  letter-spacing: 3px;
  text-transform: uppercase;
  font-family: "Coiny", sans-serif;
  line-height: 1;
  margin-top: 0.8rem;
}

.btn-holder {
  display: flex;
  justify-content: center;
}

.category {
  margin-bottom: 16px;
}

.fact {
  font-size: 1.25rem;
  line-height: 1.4;
  background-color: #44403c;
  margin-bottom: 1rem;
  padding: 1rem 1.5rem;
  letter-spacing: -1px;
  border-radius: 1rem;
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.tag {
  text-transform: uppercase;
  font-size: 0.9rem;
  font-family: "Coiny", sans-serif;
  padding: 0.5rem 0.6rem 0.6rem;
  border-radius: 6.2rem;
}

ul {
  list-style: none;
}

.source:link,
.source:visited {
  color: #a8a29e;
  text-decoration: none;
  margin-left: 0.7rem;
  transition: 0.3s;
}

.source:hover,
.source:active {
  color: #3b82f6;
}

@media (max-width: 1200px) {
  .fact {
    flex-direction: column;
    align-items: flex-end;
    gap: 12px;
  }
}
@media (max-width: 1000px) {
  h1 {
    font-size: 2.5rem;
  }
  .fact-form {
    flex-direction: column;
    align-items: stretch;
  }
  .fact-form input,
  .fact-form select {
    width: auto;
  }
  .aside {
    max-height: 200px;
    overflow-y: scroll;
  }
}
@media (max-width: 900px) {
  .main {
    grid-template-columns: 1fr;
  }
  .logo {
    gap: 0rem;
  }
}
@media (max-width: 730px) {
  h1 {
    font-size: 2rem;
    gap: 1rem;
  }
  .logo {
    gap: 1rem;
  }
  .logo img {
    width: 5rem;
    height: 5rem;
  }
  .aside {
    max-height: 200px;
    overflow-y: scroll;
  }
}

