@media (max-width: 1200px) {
	.fact {
		flex-direction: column;
		align-items: flex-end;
		gap: 12px;
	}
}
@media (max-width: 1000px) {
	h1 {
		font-size: 2.5rem;
	}
	.fact-form {
		flex-direction: column;
		align-items: stretch;
	}

	.fact-form input,
	.fact-form select {
		width: auto;
	}
	.aside {
		max-height: 200px;
		overflow-y: scroll;
	}
}
@media (max-width: 900px) {
	.main {
		grid-template-columns: 1fr;
	}
	.logo {
		gap: 0rem;
	}
}

@media (max-width: 730px) {
	h1 {
		font-size: 2rem;
		gap: 1rem;
	}
	.logo {
		gap: 1rem;
		img {
			width: 5rem;
			height: 5rem;
		}
	}
	.aside {
		max-height: 200px;
		overflow-y: scroll;
	}
}
