* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #292524;
  color: #fafaf9;
  font-family: "Sono", sans-serif;
  padding: 3rem 4rem 0;
}
#root {
  max-width: 1500px;
  margin: 0 auto;
}

.header {
  margin-bottom: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main {
  height: calc(100vh - (120px + 48px));
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 3rem;
}

section {
  overflow: scroll;
  padding-bottom: 40px;
}
