.btn {
	border: none;
	font-family: 'Coiny', sans-serif;
	line-height: 1;
	text-transform: uppercase;
	font-size: 1rem;
	padding: 1rem 0 0.9rem;
	background-color: #f982aa;
	color: inherit;
	border-radius: 6.2rem;
	cursor: pointer;
	transition: 0.3s;
}

.btn:hover {
	transform: scale(90%);
}

.btn-large {
	font-size: 1.3rem;
	padding: 1.3rem 2rem 1rem;
}

.btn-share {
	margin-bottom: 2rem;
	padding: 1rem 5rem;
}

.btn-all-categories {
	margin-bottom: 1rem;
	width: 100%;
}

.btn-category {
	width: 100%;
	background-image: none;
}

.vote-buttons {
	margin-left: auto;
	flex-shrink: 0;

	display: flex;
	gap: 0.5rem;
	button {
		border: none;
		background-color: #78716c;
		font-size: 1.1rem;
		padding: 0.3rem 0.7rem;
		border-radius: 6.2rem;
		color: inherit;
		font-family: inherit;
		font-weight: 600;
		cursor: pointer;
		transition: 0.3s;
	}
	button:hover {
		background-color: #292524;
	}
}
