.hidden {
  display: none !important;
}

.logo {
  display: flex;
  align-items: center;
  gap: 1rem;
  img {
    width: 5rem;
    height: 5rem;
  }
}

h1 {
  font-size: 3rem;
  letter-spacing: 3px;
  text-transform: uppercase;
  font-family: "Coiny", sans-serif;
  line-height: 1;
  margin-top: 0.8rem;
}
.btn-holder {
  display: flex;
  justify-content: center;
}

.category {
  margin-bottom: 16px;
}

.fact {
  font-size: 1.25rem;
  line-height: 1.4;
  background-color: #44403c;
  margin-bottom: 1rem;
  padding: 1rem 1.5rem;
  letter-spacing: -1px;
  border-radius: 1rem;

  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.tag {
  text-transform: uppercase;
  font-size: 0.9rem;
  font-family: "Coiny", sans-serif;
  padding: 0.5rem 0.6rem 0.6rem;
  border-radius: 6.2rem;
}

ul {
  list-style: none;
}

.source:link,
.source:visited {
  color: #a8a29e;
  text-decoration: none;
  margin-left: 0.7rem;
  transition: 0.3s;
}

.source:hover,
.source:active {
  color: #3b82f6;
}
